import React from 'react'
import { AppContext } from './Store';
import {withRouter} from 'react-router';

const HeroPageContext = React.createContext();

export {HeroPageContext}

function heroPageReducer(state, action) {
    switch(action.type) {
        case 'UPDATE_CAREER':
            state.history.push(`/heroes/${action.payload.careerId}`);
            return {...state, careerId: action.payload.careerId};
        case 'UPDATE_TALENTS':
            var newTalents = {...state.talents};
            newTalents = newTalents[0] < 0 ? [0,0,0,0,0,0] : newTalents;
            newTalents[action.payload.tier-1] = action.payload.talent;
            return {...state, talents: newTalents};
        case 'UPDATE_ALL_TALENTS':
            return {...state, talents: action.payload};
        default:
            throw new Error('Error updating Hero Page state.');
        }
}

export default withRouter(function HeroPageStore(props) {

    const stateHook = React.useReducer(heroPageReducer, {
        history: props.history,
        careerId: 0,
        talents: [-1,-1,-1,-1,-1,-1],
        melee: {
            id: 1,
            property1Id: 1,
            property1Value: 10,
            property2Id: 2,
            property2Value: 10,
            traitId: 1
        },
        range: {
            id: 1,
            property1Id: 1,
            property1Value: 10,
            property2Id: 2,
            property2Value: 10,
            traitId: 1
        },
        necklace: {
            id: 1,
            property1Id: 1,
            property1Value: 10,
            property2Id: 2,
            property2Value: 10,
            traitId: 1
        },
        charm: {
            id: 1,
            property1Id: 1,
            property1Value: 10,
            property2Id: 2,
            property2Value: 10,
            traitId: 1
        },
        trinket: {
            id: 1,
            property1Id: 1,
            property1Value: 10,
            property2Id: 2,
            property2Value: 10,
            traitId: 1
        }
    });

    return (
        <AppContext.Provider value={stateHook}>
            {props.children}
        </AppContext.Provider>
    )
});